* {
  box-sizing: border-box;
}
.container {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: space-between;
  height: 100%;
  width: 412px;
  background-color: rgba(201, 201, 189, 0.311);
  border: solid 1px #2F97C1;
}
.NavBar {
  width: 100%;
  height: 10%;
  align-self: flex-start;
  background-color: #2f97c1;
}
.Home {
  height: 100%;
}
.Footer {
  width: 100%;
  height: 10%;
  display: flex;
  justify-content: center;
  background-color: #2F97C1;
  flex: 0 1 auto;
}
